				YTPlayer.opt.mute = true;
				YTPlayer.hasData = false;
				YTPlayer.hasChanged = true;
				YTPlayer.player.loopTime = undefined;
	
				if( opt ) jQuery.extend( YTPlayer.opt, YTPlayer.defaultOpt, opt );
