					var $YTPlayer = jQuery( YTPlayer );
					var data = YTPlayer.opt;
					var startAt = YTPlayer.opt.startAt ? YTPlayer.opt.startAt : 1;
					var stopAt = YTPlayer.opt.stopAt > YTPlayer.opt.startAt ? YTPlayer.opt.stopAt : 0;
