			var newArray = arr.slice();
			var len = newArray.length;
			var i = len;
			while( i-- ) {
				var p = parseInt( Math.random() * len );
				var t = newArray[ i ];
				newArray[ i ] = newArray[ p ];
				newArray[ p ] = t;
			}
